.toggle-switch {
  width: 44px;
  height: 18px;
  position: relative;
}

.toggle-input {
  display: none;
}

.toggle-label {
  width: 100%;
  height: 100%;
  cursor: pointer;
  background: linear-gradient(to right, #fb0, #f70);
  border-radius: 30px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.toggle-label:before {
  content: "";
  width: 14px;
  height: 14px;
  background: #fff;
  border-radius: 50%;
  transition: transform .3s;
  position: absolute;
  top: 2px;
  left: 2px;
}

.toggle-input:checked + .toggle-label:before {
  transform: translateX(24px);
}

.toggle-icon {
  width: 12px;
  height: 12px;
  color: #fff;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  display: flex;
  position: absolute;
}

.moon {
  left: 2px;
}

.sun {
  right: 2px;
}

@media (min-width: 768px) {
